/* Viewer Hint */

.viewer-hint {
  pointer-events: none;
  position: absolute;
  bottom: 25px;
  left: 50%;
  z-index: 1;
  font-size: 13px;
  color: #fff;
  text-shadow: 0 0 5px #000;
  transition: opacity 0.2s;
  transform: translate(-50%);
}

.viewer-hint > div {
  background-image: radial-gradient(ellipse at center, rgba(0, 0, 0, 0.3) 0, transparent 66%);
  animation: back-n-forth infinite 5s ease-in-out;
  display: flex;
  flex-direction: column;
  gap: 10px;
  text-align: center;
}

.viewer-hint.hint-hide {
  opacity: 0;
}

@keyframes back-n-forth {
  0% {
    transform: translateX(-30px);
  }

  50% {
    transform: translateX(30px);
  }

  100% {
    transform: translateX(-30px);
  }
}

.viewer-hint .icon {
  display: inline-block;
  width: 80px;
  height: 70px;
  background: url('../click-nd-hold.svg');
  background-size: 80px 70px;
}

/* Loader */

.loader {
  position: relative;
}

.loader-outer-circle {
  animation: 2s antiClockwiseSpin infinite linear;
}

@keyframes antiClockwiseSpin {
  from {
    transform: rotate(360deg);
  }
  to {
    transform: rotate(0deg);
  }
}
.loader-inner-circle {
  position: absolute;
  left: 12px;
  top: 12px;
  animation: 2s clockwiseSpin infinite linear;
}

@keyframes clockwiseSpin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes back-n-forth {
  0% {
    transform: translateX(-30px);
  }

  50% {
    transform: translateX(30px);
  }

  100% {
    transform: translateX(-30px);
  }
}

